.header {
  padding: 40px 0 8px;
}

.header_wrp {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header_title {
  color: var(--text-color);
  margin: 0;
  font-size: 16px;
  font-weight: 400;
}

.search-input-wrp {
  border: 1px solid #000;
  border-radius: 10px;
  align-items: center;
  width: 404px;
  height: 24px;
  padding: 10px 10px 10px 0;
  display: flex;
  overflow: hidden;
}

#search-input {
  background-color: #0000;
  border: none;
  outline: none;
  width: 100%;
  height: 100%;
  padding: 0 0 0 5px;
}

.search-btn {
  color: var(--text-color);
  cursor: pointer;
  border-radius: 10px;
  padding: 12px;
  font-size: 20px;
}

.search-btn:hover {
  color: #f7e8d3;
  background-color: #d8ad50;
}

.switch_container {
  display: flex;
}

#switch {
  display: none;
}

.switch-label {
  width: 64px;
  height: 25px;
  transition: .3 ease;
  cursor: pointer;
  background-color: #895710;
  border-radius: 2rem;
  position: relative;
  overflow: hidden;
}

.switch-label:before {
  content: "";
  background-color: #f7e8d3;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  transition: all .8s;
  display: block;
  position: absolute;
}

.switch-icons {
  font-size: 20px;
  position: absolute;
}

.icon-sun {
  top: 12%;
  left: -31%;
}

.icon-moon {
  color: #f7e8d3;
  top: 9%;
  right: 9%;
}

#switch:checked + .switch-label:before {
  transform: translate(39px);
}

#switch:checked + .switch-label {
  background-color: #d8ad50;
}

#switch:checked + .switch-label .icon-sun {
  left: 11%;
}

#switch:checked + .switch-label .icon-moon {
  right: -24%;
}

.sidebar {
  background-color: var(--sidebar);
  z-index: 10;
  border-radius: 0 20px 20px 0;
  flex-direction: column;
  width: 138px;
  height: 100vh;
  transition: width .3s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
  box-shadow: 2px 0 6px #76767536;
}

.sidebar_title {
  opacity: 0;
  text-align: center;
  color: var(--text-color);
  margin-top: 160px;
  font-size: 24px;
  transition: opacity .3s;
}

.sidebar_content {
  flex-direction: column;
  gap: 32px;
  padding: 0 0 0 57px;
  list-style: none;
  display: flex;
}

.content_item {
  align-items: center;
  font-size: 16px;
  text-decoration: none;
  transition: background-color .3s, border-radius .3s, padding .3s;
  display: flex;
}

.content_item:hover {
  background-color: #d8ad50;
  border-radius: 10px;
  padding: 8px;
}

#sidebar_toggle:not(:checked) ~ .sidebar .content_item:hover {
  padding: initial;
  border-radius: initial;
  background-color: #0000;
}

#sidebar_toggle:not(:checked) ~ .sidebar .content_item:hover i {
  transform: scale(1.2);
}

.content_item span {
  color: var(--text-color);
  opacity: 0;
  white-space: nowrap;
  margin-left: 10px;
  transition: opacity .3s;
}

.icon-note, .icon-calendar, .icon-settings {
  color: var(--text-color);
  text-align: center;
  width: 24px;
  font-size: 24px;
  transition: color .3s, transform .3s;
}

.sidebar_btn {
  z-index: 11;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  padding: 12px;
  display: flex;
  position: absolute;
  top: 40px;
  left: 44px;
}

.sidebar_btn:hover {
  background-color: #d8ad50;
  border-radius: 10px;
}

.sidebar_btn span, .sidebar_btn span:before, .sidebar_btn span:after {
  background-color: var(--text-color);
  border-radius: 2px;
  width: 24px;
  height: 3px;
  transition: all .3s;
  display: block;
  position: absolute;
}

.sidebar_btn span:before {
  content: "";
  top: -8px;
}

.sidebar_btn span:after {
  content: "";
  top: 8px;
}

#sidebar_toggle:checked + .sidebar_btn span {
  transform: rotate(45deg);
}

#sidebar_toggle:checked + .sidebar_btn span:before {
  top: 0;
  transform: rotate(90deg);
}

#sidebar_toggle:checked + .sidebar_btn span:after {
  opacity: 0;
}

#sidebar_toggle:checked ~ .sidebar {
  width: 226px;
}

#sidebar_toggle:checked ~ .sidebar .content_item span, #sidebar_toggle:checked ~ .sidebar .sidebar_title {
  opacity: 1;
}

#sidebar_toggle {
  position: absolute;
}

.page-blur-overlay {
  pointer-events: none;
  backdrop-filter: none;
  z-index: 5;
  transition: opacity .3s;
  position: fixed;
  inset: 0;
}

#sidebar_toggle:checked ~ .page-blur-overlay {
  pointer-events: auto;
  opacity: 1;
  backdrop-filter: blur(6px);
  background-color: #00000026;
}

.page-content {
  z-index: 1;
  position: relative;
}

